@import '~antd/dist/antd.dark.css';

.logo {
  float: left;

  img {
    height: 30px;
    padding: 0px 4px;
    margin-top: -8px;
  }
}

.ant-layout-header {
  padding: 0px 5px;

  a {
    text-decoration: none;
  }

  .ant-menu {
    font-family: 'Futile Pro';
    text-transform: uppercase;
    font-size: x-small;
    float: right;
    letter-spacing: 1px;

    .ant-menu-item {
      padding: 0px 10px;
    }

    .ant-menu-item-active,
    .ant-menu-item-active:hover {
      background-color: darken(#e99766, 40);
    }

    .ant-menu-item::selection,
    .ant-menu-item-disabled,
    .ant-menu-item-disabled:hover {
      background-color: inherit;
    }
  }
}

.ant-layout-content {
  margin: 12px 12px 0;
}
